<template>
  <div id="app">
    <input v-model="owneraddr" placeholder="input owner eth addr" @keyup.enter="refreshresult">
    <button v-on:click="refreshresult">Refresh Alchemy ENS cache</button>
    <p>input owner is: {{ owneraddr }}</p>
    <span id="result" style="display:inline-block;word-wrap:break-word;"></span>
  </div>
</template>

<script>
import { refreshens } from './lib/refresh_ens'
export default {
  name: 'app',
  data () {
    return {
       owneraddr: "",
    }
  },
  methods: {
    refreshresult: async function () {
      refreshens(this.owneraddr).then(r => document.getElementById("result").appendChild(document.createElement("br")))
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: start;
  color: #2c3e50;
  margin-top: 60px;
}

</style>



