import { getNftsForOwner, refreshNftMetadata, initializeAlchemy } from "@alch/alchemy-sdk"
const alchemy = initializeAlchemy();

const contractAddress = '0x57f1887a8bf19b14fc0df6fd9b2acc9af147ea85';

export const getENS = async (owner) => {
    let getENSResponse
    try{
        getENSResponse = await getNftsForOwner(alchemy, owner)
    }
    catch(e){
        printLogOnScreen(e)
        return
    }
        
    const ENSTokenInfo = getENSResponse.ownedNfts.filter(item => item.contract.address == contractAddress)
    let ENStokenIDs = []
    ENSTokenInfo.forEach(e => {
        ENStokenIDs.push(e.tokenId)
        printLogOnScreen(`tokenID and ens: ${e.tokenId}, ${e.rawMetadata.name}`)
    })
    return ENStokenIDs

}

export async function refreshens(owner) {
    printLogOnScreen("starting refresh: ")
    const tokenIds = await getENS(owner)
    printLogOnScreen(`owner: ${owner}`)
    printLogOnScreen(`ENStokenIDs length: ${tokenIds.length}`)
    let refreshresponse
    for (let i = 0; i < tokenIds.length; i++) {
        refreshresponse = await refreshNftMetadata(alchemy, contractAddress, tokenIds[i]);
        printLogOnScreen(`to refresh ens tokenID: ${tokenIds[i]}`)
        printLogOnScreen(`refresh response: ${refreshresponse}`)
    }

    printLogOnScreen(`after refreshed: `)
    await getENS(owner)
    return refreshresponse
}

function printLogOnScreen(str) {
    document.getElementById("result").append(str)
    document.getElementById("result").appendChild(document.createElement("br"));
}



